
.slider_book
{	
	width: 250px;
	padding:10px;
	display: flex;
	margin: 3px 3px;
	flex-direction: column;
	flex-wrap: wrap;	
}

.slider_book-img 
{
		margin: 0 auto;
}

.slider_book-img img
{
	width: 100%;
	box-shadow: 0 10px 20px -6px rgba(0, 0, 0, 0.5);
	object-fit: contain;
	border-radius: 10px;

}

.slider_book-name p,
.slider_book-price p
{	
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0;
    overflow: hidden;
}
.slider_book-author p
{
	font-size: var(--small_text);
}

.slider_book-price
{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-self: normal;
    align-items: center;
}
.slider_book-price p
{	margin-left: 12px;	
	color: red;
}
.slider_book-button
{
	color:var(--green);
    font-size: 16px;
    height: 29px;
    width: 55px;
    background: transparent;    
    border: 1px solid;
    border-radius: 25px;
    outline: none;
}
@media (max-width:800px)
{
.slider_book
{
	width: 159px;
    flex-grow: 1;

}
.slider_book-name
{
	    width: 100%;
}	
.slider_book-price p
{ 
    font-size: var(--heading_text);
}
.slider_book-name:first-child
{
	font-size: var(--heading_text);
}

.slider_book-img img
{
	width: auto;
	height:159px;
	object-fit: contain;
}


	
}
