.searchresult_container
{
	display: flex;
    border-radius: 10px;
    padding: 12px;
    width: 90%;
    margin: auto;
}

.searchresult_img
{
	
}

.searchresult_img img
{
	width: 200px;
	box-shadow: 0 10px 20px -6px rgba(0, 0, 0, 0.5);
	border-radius:10px;
	object-fit: contain;
}
.searchresult_desc
{
	display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 0 10px;
}

.searchresult_desc-name
{
}

.searchresult_desc-price p
{
	color:red;
}
.searchresult_desc-author
{
	font-size:var(--small_text);
}

.searchresult_button input
{
	padding: 9px;
	max-width: 140px;
	border-radius: 10px;
	background-color: var(--third_color);
	outline: none;
	border: 0;

}

@media (max-width:768px)
{
.searchresult_container
{
	width: 95%;
}
.searchresult_img img
{
	width: 100px;
	object-fit: contain;
}

.searchresult_desc-name h4
{
	font-size: var(--heading_text);
}
.searchresult_desc-price
{
	margin-top:10px;
}
.searchresult_button input
{
	padding: 5px;
	font-size: var(--normal_text);

}
}