.bookdetail_container
{
	background: white;
	border-radius:10px;
	padding: 10px;
    margin: 50px auto;
    width: 80%;
    display: flex;
}
.bookdetail_img
{	
    width: 45%;
    position: relative;
    left: -61px;
    top: 33px;
}
.bookdetail_img img
{
	box-shadow: -14px 12px 7px black;
	width: 100%;
	object-fit: contain;
}

.bookdetail
{
	width: 55%;
	margin-top: 33px;

}

.bookdetail_name
{
	font-size: 22px;
	margin: 30px 0;

}

.bookdetail_price
{
	color:red;	
	font-size: 25px;
	margin: 30px 0;

}
.bookdetail_desc
{
	margin: 30px 0;
    font-size: 18px;

}
.bookdetail_desc ol
{
	list-style: none;
	padding:0;
}

.bookdetail_button
{
	margin: 30px 0;
}
.buy_button
{
	
	height: 45px;
	max-width: 150px;   
    border-radius: 10px;
    color:white;
    outline: none;
    border: 0;
	background-color: #35df3c ;
}




/*//for mobile*/
@media (max-width:768px)
{
.bookdetail_container
{
	width: 95%;
	flex-wrap:wrap;
}
.bookdetail_img
{
	position: initial;
	width: auto;
	margin: auto;
}
.bookdetail_img img
{
	object-fit: contain;
	width: auto;
	height: 220px;
}

.bookdetail
{
	width: 100%;
	margin: 0;
}
.bookdetail_name
{
	font-size: var(--heading_text);
}

.bookdetail_price
{
	font-size: var(--bigheading_text);
	
}
.bookdetail_desc
{
   font-size: var(--normal_text);

}

}

